 import { Component, OnInit } from '@angular/core';
import {Pdf,ServicioService}from './../../services/servicio.service';
import { ActivatedRoute, Router } from '@angular/router';
import fac from'./../../../assets/json/factores.json';
@Component({
  selector: 'app-solido',
  templateUrl: './solido.page.html',
  styleUrls: ['./solido.page.scss'],
})
export class SolidoPage implements OnInit {
  section = '';
  aux= ["", "","",""];
  show_input_paso1;
  show_input_paso2;
  FactoresPersonaje:any;
  p1=true;p2=true;p3=true;
  objaux:Pdf;
  show= false;
  ejemplo; lista; gaseoso = false;
  Paso1:string; 
  Paso2:string;
  Paso3_1:string;
  Paso3_2:string;
  Paso3_3:string;
  Paso3_4:string;
  personajesModeR = 'FONDO-ALEATORIO';
  personajesArr = ['crispi', 'cori','cesia','carmel','cristal']; 
  personajeSeleccionado = '';
  personajeSeleccionadoaux = '';
  numeroPersonaje:any;
  pasoSelecionado = '';
  acercaElCelular = false;
  show_text_paso1 = false;
  show_text_paso2 = false;
  show_text_paso3 = false;
  show_input_paso3;
  pdf:Pdf={
    id :'',
    num:'',
    nombre:'',
    ambiente:'',
    factor:'',
    personaje:'',
    solitario:true,
    solido:'',
    gaseoso:'',
    liquido:'',
    P1:'',
    P2:'',
    P3_1:'',
    P3_2:'',
    P3_3:'',
    P3_4:'',
    tarjeta:'',
    tarjeta2:'',
    idea1:'',
    idea2:'',
    idea3:'',
    idea4:'',
    ideaselecionada:'',
    aprendizaje:'',
    V:'',
    C:'',
    R:'',
    P:'',
    tipo:'',
    img:''
  };
  constructor(private serv:ServicioService,
    private router:Router,
    private route: ActivatedRoute) {}

 async ngOnInit() {
    this.section = 'comenzar';
    this.FactoresPersonaje=fac;
    this.selecionador(this.personajeSeleccionado);
  }
  async iniciador()    
  {
  this.mipromesa();
}
selectorfactores(){
  let a=this.personajeSeleccionadoaux.toLowerCase();
  switch (a) {
        case 'crispi':
          this.numeroPersonaje=0;
              break;
        case 'cesia':
          this.numeroPersonaje=1;
              break;
        case 'cori':
          this.numeroPersonaje=2;
              break;
        case 'carmel':
          this.numeroPersonaje=3;
              break;
        case 'cristal':
          this.numeroPersonaje=4;
              break;
            default:
              break;
      }
      for(var i = 0 ;i<this.aux.length; i++)  {  
      this.aux[i]=this.FactoresPersonaje.fac[this.numeroPersonaje].tarjetas[i+1];
       }

  }
async ngAfterViewInit(){
    this.route.params.subscribe(params => {
      const _params = params.section;   
      const _personaje= params.personajeSeleccionado;
      this.personajeSeleccionado=_personaje;
      this.section = _params;
    });
    this.selecionador(this.personajeSeleccionadoaux);
    if((this.section=='paso1')||(this.section=='s1')||(this.section=='paso2')||(this.section=='paso3'))
      {
        this.promesafactor();
      }

  }
  avanzar(section){
    this.section = section;
  }

  personajesMode(forma){
    this.personajesModeR = `FONDO-${forma}`;
  }

  personajeRandom(){
    const random = Math.floor(Math.random()*5);
    this.personajeSeleccionadoaux = this.personajesArr[random].toUpperCase();
    this.pdf.personaje=this.personajeSeleccionadoaux;
  }
selecionador(item){   
  this.mipromesa().finally(()=>{
    const DEFAULT_SELECT='';
    const NUM_SELECT={
      0:this.personajesArr[0],
      1:this.personajesArr[2],
      2:this.personajesArr[1],
      3:this.personajesArr[3],
      4:this.personajesArr[4]
    }
    const number = NUM_SELECT[item] || DEFAULT_SELECT;
    this.personajeSeleccionadoaux = number.toUpperCase();
    this.pdf.personaje= this.personajeSeleccionadoaux;
  });
  }
  guardarpersonaje(){
    this.pdf.personaje = this.personajeSeleccionadoaux;
  }
  factores(){
    const DEFAULT_SELECT='';
    const OP_SELECT={
      'T':this.pdf.factor =this.aux[0],
      'I':this.pdf.factor =this.aux[1],
      'RN':this.pdf.factor =this.aux[2],
      'CB':this.pdf.factor =this.aux[3]
    }
    const number= OP_SELECT[this.pasoSelecionado] || DEFAULT_SELECT;
    this.pasoSelecionado=number;
      this.mipromesa().finally(()=>{
        this.pdf.factor =number;
        this.pdf.solido ='Iniciado';
        this.pdf.personaje= this.personajeSeleccionadoaux;
        this.serv.AddItem(this.pdf);
      });
  }

  async saveInputPaso1(){
    await this.mipromesa().then(()=>{
      this.pdf.P1=this.Paso1;
      this.serv.AddItem(this.pdf);
    });
  }
  async saveInputPaso2(){
    await this.mipromesa().then(()=>{
      this.pdf.P2=this.Paso2;
      this.serv.AddItem(this.pdf);
    });
  }
  async saveInputPaso3(){
    await this.mipromesa().then(()=>{
    this.pdf.P3_1=this.Paso3_1;
    this.pdf.P3_2=this.Paso3_2;
    this.pdf.P3_3=this.Paso3_3;
    this.pdf.P3_4=this.Paso3_4;
    this.serv.AddItem(this.pdf);
    this.serv.subir2(this.pdf);
  })
  }
  openPopUP(item){
    switch (item) {
      case 'lista':
        this.lista = !this.lista;
            break;
          case 'ejemplo':
            this.ejemplo = !this.ejemplo;
                break;
          default:
            break;
    }
    this.show = !this.show;
  }
  openPopUP2(item){
    switch (item) {
        case 'p1':
          this.p1= !this.p1;
          break;
        case 'p2':
          this.p2= !this.p2;
          break;
        case 'p3':
          this.p3= !this.p3;
          break;
        default:
          break;
    }
  }
  alLector(){
    this.router.navigateByUrl('scanner');
  }
  redireccionador(item){
    this.router.navigateByUrl("solido/"+item+"/"+this.personajeSeleccionado);
  }
  atras(){
    this.router.navigateByUrl("home/ambiente");
  }
  eliminar(){
    this.serv.deleteItem();
  }

  async mipromesa(){
     new Promise((resolve) => {
      this.objaux = JSON.parse(this.serv.getItem('_cap_pdf2'));
      if(this.objaux.num!=""){
        this.pdf=  this.objaux; 
        return resolve(this.pdf);
      }
    });
  }
  async promesafactor(){
    new Promise((resolve)=>{
      if(this.objaux.num!=""){
      this.pdf=  this.objaux; 
      this.pasoSelecionado = this.pdf.factor;
      return resolve(this.pdf);
    }
    });
  }
}
